import Component from '../react/Component'

const ReactDOM = {
  render
}

function render (vnode, container) {
  return container.append(_render(vnode))
}

// è¿”å›žè¦æ¸²æŸ“
function _render(vnode) {
  console.log(vnode)
  // vnodeä¸ºç©ºã€ä¸ºundefinedã€ä¸ºboolean
  if (vnode === null 
    || vnode === undefined 
    || typeof vnode === 'boolean') 
    return
  // vnodeä¸ºå­—ç¬¦ä¸²
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return textNode
  }

  // ç»„ä»¶
    // åˆ›å»ºç»„ä»¶
    // è®¾ç½®å±žæ€§
    // æ¸²æŸ“ç»„ä»¶

  if (typeof vnode.tag === 'function') {
    const comp = createComponent(vnode.tag, vnode.attrs)
    setComponentProps(comp, vnode.attrs)
    return comp.base
  }

  // vnodeä¸ºobject
    // åˆ›å»ºä¸€ä¸ªå…ƒç´ 
    // éåŽ†åŠ ä¸Šå±žæ€§
    // appendåˆ°containerä¸Š
  if (typeof vnode === 'object') {
    const {tag, attrs} = vnode
    let dom = document.createElement(tag)
    if (attrs) {
      Object.keys(attrs).map(key => {
        setAttribute(dom, key, attrs[key])
      })
    }

    vnode.children && vnode.children.forEach(child => render(child, dom))

    return dom
  }
}

// åˆ›å»ºç»„ä»¶
function createComponent(comp, props) {
  // ç±»ç»„ä»¶ç›´æŽ¥åˆ›å»ºè¿”å›ž
  // å‡½æ•°ç»„ä»¶å°±è¦æž„é€ æˆç±»ç»„ä»¶
  let instance
  if(comp.prototype && comp.prototype.render) {
    instance = new comp(props)
  } else {
    instance = new Component(props)
    instance.constructor = comp
    instance.render = function () {
      return this.constructor(props)
    }
  }

  return instance
}

// è®¾ç½®ç»„ä»¶
function setComponentProps(comp, props) {
  comp.attrs = props
  renderComponent(comp)
}

// æ¸²æŸ“ç»„ä»¶
function renderComponent(comp) {
  const renderer = comp.render()
  comp.base = _render(renderer)
  return comp
}

// è®¾ç½®å±žæ€§
function setAttribute (dom, key, value) {
  // className => class
  if (key === 'className') {
    key = 'class'
  } else if (/on\w+/.test(key)) {
    // onCLick => click
    key = key.toLowerCase()
    dom[key] = value || ''
  } else if (key === 'style') {
    // style
    // style="color: 'red'"
    // style={{color: 'red'}}
    if (!value && typeof value ==='string') {
      dom.style.cssText = value
    } else if (typeof value === 'object') {
      for(let key in value) {
        if(typeof value[key] === 'number') {
          dom.style[key] =  value[key] + 'px'
        } else {
          dom.style[key] =  value[key]
        }
      }
    }
  } else {
    // å…¶ä»–çš„dom[key] = valueï¼Œ æ›´æ–°
    if (key in dom) {
      dom[key] = value || ''
    }

    if(value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key)
    }
  }
}

export default ReactDOM